'use strict';

/**
* Typed array constructor which returns a typed array representing an array of twos-complement 8-bit signed integers in the platform byte order.
*
* @module @stdlib/array-int8
*
* @example
* import ctor from '@stdlib/array-int8';
*
* var arr = new ctor( 10 );
* // returns <Int8Array>
*/

// MODULES //

import hasInt8ArraySupport from '@stdlib/assert-has-int8array-support';
import builtin from './main.js';
import polyfill from './polyfill.js';


// MAIN //

var ctor;
if ( hasInt8ArraySupport() ) {
	ctor = builtin;
} else {
	ctor = polyfill;
}


// EXPORTS //

export default ctor;
